import { apiBaseUrl } from './constants';

export class WorkdayClient {
  private jobs: Job[] | null;
  constructor() {
    this.jobs = null;
  }

  public async fetchJobs(): Promise<Job[] | null> {
    if (this.jobs) {
      return Promise.resolve(this.jobs);
    }
    const { Report_Entry } = (await this.fetchJobsFromAPI()) || {};
    if (Report_Entry) {
      this.jobs = Report_Entry;
    }

    return Promise.resolve(this.jobs);
  }

  private async fetchJobsFromAPI(): Promise<JobResponse | null> {
    try {
      const response = await fetch(`${apiBaseUrl}jobs`);
      const jobs = await response.json();

      return jobs;
    } catch (error) {
      console.error('Error fetching jobs', error);
      return null;
    }
  }
}
