import { ACCESSORS, SELECTORS } from './constants';
import { Dropdown } from './dropdown';
import { getJobTitles, getLocations } from './util';

export const dropdownCTAs = (
  jobs: Job[],
  onSearchBtnClicked: (
    jobs: Job[],
    selections: {
      selectedLocations: string[];
      selectedTitles: string[];
    }
  ) => void
) => {
  // Location search
  const locationSearch = document.querySelector<HTMLDivElement>(SELECTORS.locationInput);
  const locations = getLocations(jobs);
  let locationDropdown: Dropdown | undefined;
  if (locationSearch && locations) {
    locationDropdown = new Dropdown(locationSearch, locations);
  }

  // Job title search
  const jobTitleSearch = document.querySelector<HTMLDivElement>(SELECTORS.titleInput);
  const jobTitles = getJobTitles(jobs);
  let titleDropdown: Dropdown | undefined;
  if (jobTitleSearch && jobTitles) {
    titleDropdown = new Dropdown(jobTitleSearch, jobTitles);
  }

  // Filter jobs based on selected locations and titles
  const filterJobs = (jobs: Job[]) => {
    let filteredJobs = jobs;
    if (locationDropdown) {
      filteredJobs = locationDropdown.filterJobs(filteredJobs, ACCESSORS.location);
    }
    if (titleDropdown) {
      filteredJobs = titleDropdown.filterJobs(filteredJobs, ACCESSORS.title);
    }
    return filteredJobs;
  };

  // Search button
  const searchButton = document.querySelector<HTMLButtonElement>(SELECTORS.searchButton);
  if (searchButton) {
    searchButton.addEventListener('click', () => {
      const selectedLocations = locationDropdown?.getSelectedItems() || [];
      const selectedTitles = titleDropdown?.getSelectedItems() || [];
      onSearchBtnClicked(filterJobs(jobs), { selectedLocations, selectedTitles });
    });
  }
};
