import { ATTRIBUTES, SELECTORS } from './constants';

export class Dropdown {
  private searchInput: HTMLInputElement | undefined;
  private listItem: HTMLDivElement | undefined;
  private searchItems: HTMLDivElement[] = [];
  constructor(
    private container: HTMLDivElement,
    private data: string[]
  ) {
    this.bindData();
  }

  /**
   *
   * @returns the selected items
   */
  public getSelectedItems(): string[] {
    return this.searchItems
      .filter((searchItem) => searchItem.hasAttribute(ATTRIBUTES.dataChecked))
      .map((searchItem) => {
        const textElement = searchItem.querySelector(SELECTORS.name);
        return textElement?.textContent || '';
      });
  }

  public filterJobs(jobs: Job[], accessor: (job: Job) => string): Job[] {
    const selectedItems = this.getSelectedItems();
    if (selectedItems.length === 0) return jobs;
    return jobs.filter((job) => selectedItems.includes(accessor(job) || ''));
  }

  public reset() {
    this.searchItems.forEach((searchItem) => {
      searchItem.removeAttribute(ATTRIBUTES.dataChecked);
      // remove the checked class 'w--redirected-checked'
      const div = searchItem.querySelector('div');
      if (div) div.classList.remove(ATTRIBUTES.checkedClass);
    });
  }

  private bindData() {
    const [inputContainer, listItemTemplate] = this.container.childNodes;

    // remove all elements after the template
    while (this.container.childNodes.length > 1) {
      const node = this.container.lastChild;
      if (node) this.container.removeChild(node);
    }

    if (!inputContainer || !listItemTemplate) return;
    this.searchInput = (inputContainer as HTMLElement).querySelector('input') as HTMLInputElement;
    this.generateListItems(listItemTemplate);
    this.handleSearch();
  }

  /**
   * Generates the list items based on the data
   * @param listItemTemplate
   */
  private generateListItems(listItemTemplate: ChildNode) {
    this.listItem = listItemTemplate.cloneNode(true) as HTMLDivElement;

    this.data.forEach((item) => {
      const searchItem = this.createListItem();
      if (!searchItem) return;

      const textElement = searchItem.querySelector(SELECTORS.name);
      if (textElement) textElement.textContent = item;

      this.handleItemClick(searchItem);

      this.container.appendChild(searchItem);
      this.searchItems.push(searchItem);
    });
  }

  /**
   *  Responsible for handling the click event on the list item
   * @param searchItem - the list item
   */
  private handleItemClick(searchItem: HTMLDivElement) {
    searchItem.addEventListener('click', (event: MouseEvent) => {
      const div = searchItem.querySelector('div');
      let isChecked = false;
      if (div) {
        isChecked = !div.classList.contains(ATTRIBUTES.checkedClass);

        if (isChecked) {
          searchItem.setAttribute(ATTRIBUTES.dataChecked, isChecked.toString());
        } else {
          searchItem.removeAttribute(ATTRIBUTES.dataChecked);
        }
      }
      event.stopPropagation();
    });
  }

  /**
   *  Responsible for creating a cloned list item
   * @returns a cloned list item
   */
  private createListItem() {
    if (!this.searchInput || !this.listItem) return;
    const searchItem = this.listItem.cloneNode(true) as HTMLDivElement;
    return searchItem;
  }

  /**
   * Responsible for handling the search functionality
   * @returns
   */
  private handleSearch() {
    if (!this.searchInput) return;

    this.searchInput.addEventListener('click', (event) => {
      event.stopPropagation();
    });

    /**
     * This event listener is responsible for filtering the list items based on the search
     */
    this.searchInput.addEventListener('input', () => {
      const searchValue = this.searchInput?.value.toLowerCase();
      this.searchItems.forEach((searchItem) => {
        if (!searchValue) {
          searchItem.style.display = 'block';
          return;
        }

        const textElement = searchItem.querySelector(SELECTORS.name);
        if (textElement) {
          const text = textElement.textContent?.toLowerCase();
          if (text && text.includes(searchValue || '')) {
            searchItem.style.display = 'block';
          } else {
            searchItem.style.display = 'none';
          }
        }
      });
    });
  }
}
