export type JobByTypes = {
  [key: string]: Job[];
};

/**
 * Group jobs by state
 * @param jobs
 * @returns
 */
export const groupJobByState = (jobs: Job[]): JobByTypes => {
  return filterAndGroupJobs(jobs, 'State');
};

/**
 * Group jobs by middle category
 * @param jobs
 * @returns grouped jobs by middle category
 */
export const groupJobByMiddleCategory = (jobs: Job[]): JobByTypes => {
  const jobsByLowerCategory = groupJobByLowerCategory(jobs);
  const subcategories: JobByTypes = {
    Sales: [...(jobsByLowerCategory['Sales'] || [])],
    Service: [...(jobsByLowerCategory['Service'] || [])],
    Parts: [...(jobsByLowerCategory['Parts'] || [])],
    Overhead: [
      ...(jobsByLowerCategory['Operations'] || []),
      ...(jobsByLowerCategory['Administrative'] || []),
      ...(jobsByLowerCategory['Marketing'] || []),
      ...(jobsByLowerCategory['Customer Service'] || []),
    ],
    Driveway: [
      ...(jobsByLowerCategory['Body Shop'] || []),
      ...(jobsByLowerCategory['Analytics'] || []),
      ...(jobsByLowerCategory['Real Estate'] || []),
      ...(jobsByLowerCategory['Real Estate'] || []),
    ],
    'Driveway Finance': [...(jobsByLowerCategory['Finance/Accounting'] || [])],
    'Lithia Corporate': [
      ...(jobsByLowerCategory['Information Technology & Digital Innovation'] || []),
      ...(jobsByLowerCategory['Human Resources'] || []),
    ],
  };

  return subcategories;
};

/**
 * Group jobs by category
 * @param jobs
 * @returns grouped jobs by lower category
 */
export const groupJobByLowerCategory = (jobs: Job[]): JobByTypes => {
  return filterAndGroupJobs(jobs, 'EE_Categories_lower_level');
};

/**
 *
 * @param jobs to be grouped
 * @param groupKey key of the job object to group by
 * @returns grouped jobs
 */
const filterAndGroupJobs = (jobs: Job[], groupKey: keyof Job): JobByTypes => {
  const groupedJobs: JobByTypes = {};
  const jobsWithKeyPresent = jobs.filter((job) => job[groupKey]);

  jobsWithKeyPresent.forEach((job) => {
    const keyValue = job[groupKey] || '';
    if (!groupedJobs[keyValue]) {
      groupedJobs[keyValue] = [];
    }
    groupedJobs[keyValue].push(job);
  });
  return groupedJobs;
};

/**
 *
 * @param jobs list of jobs
 * @returns sorted list of unique locations
 */
export const getLocations = (jobs: Job[]): string[] => {
  const allLocations = jobs.filter((job) => job.State).map((job) => job.State || '');

  return Array.from(new Set(allLocations)).sort();
};

/**
 *
 * @param jobs list of jobs
 * @returns sorted list of unique Job titles
 */
export const getJobTitles = (jobs: Job[]): string[] => {
  const jobTitles = jobs.filter((job) => job.title).map((job) => job.title || '');

  return Array.from(new Set(jobTitles)).sort();
};

/**
 *
 * @param params query params to be set
 */
export const setQueryParams = (params: { [key: string]: string }): void => {
  const url = new URL(window.location.href);
  const queryString = new URLSearchParams();

  for (const [key, value] of Object.entries(params)) {
    if (!value) continue;
    queryString.set(key, value);
  }

  url.search = queryString.toString();

  window.history.pushState({}, '', url.href);
};

/**
 *
 * @param input string to truncate
 * @param max max length of the string
 * @returns truncated string
 */
export const truncate = (input: string, max: number = 100) => {
  if (input.length > 100) {
    return input.substring(0, max) + '...';
  }
  return input;
};
