import { buildQueryParams } from '$utils/buildQueryParams';
import { dropdownCTAs } from '$utils/dropdownCTA';
import { positionsCountMap } from '$utils/positionsCount';
import { WorkdayClient } from '$utils/WorkdayClient';

window.Webflow ||= [];
window.Webflow.push(async () => {
  const workdayClient = new WorkdayClient();

  const jobs = await workdayClient.fetchJobs();

  if (jobs) {
    positionsCountMap(jobs.length);
    dropdownCTAs(jobs, (_, selections) => {
      const { selectedLocations, selectedTitles } = selections;
      if (!selectedLocations.length && !selectedTitles.length) return;
      const query: QueryParams = {
        location: selectedLocations.join(','),
        title: selectedTitles.join(','),
      };

      const queryParams = buildQueryParams(query);
      window.location.href = `/opportunities${queryParams}`;
    });
    return;
  }
  console.error('Error fetching jobs');
});
