import { SELECTORS } from './constants';

export const positionsCountMap = (
  positionsCount: number,
  suffix = '',
  container = document.body
) => {
  const positionCountEls = container.querySelectorAll<HTMLElement>(SELECTORS.positionsCount);
  positionCountEls.forEach((el) => {
    el.textContent = positionsCount.toLocaleString() + suffix;
  });
};
